
import Box from 'vux/src/components/box/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


import {
    authConfig,
    wxConfig
} from './../config'

export default {
    components: {
        Box,
        Icon
    },
    data() {
        return {
            info: ''
        }
    },
    created() {
        let info = {
            code: this.$route.query.code,
            state: this.$route.query.state
        }

        // 1、是否同一个入口进入
        if (!info.code || info.state != authConfig.state) {
            this.setWaring()
            return
        }

        // 2、根据code校验是否该企业号成员
        this.$store.dispatch('init')
            .then(() => {
                this.$store.dispatch('getWxUser', info.code)
                    .then((userInfo) => {
                        // 非企业成员
                        if (!userInfo || !userInfo.UserId) {
                            this.setWaring()
                            return
                        }

                        // 缓存身份信息
                        sessionStorage.setItem('wxInfo', JSON.stringify({
                            code: info.code,
                            userId: userInfo.UserId
                        }))

                        this.$router.replace('/Topic')
                    })
                    .catch(() => this.setWaring())
            })
            .catch(() => this.setWaring())

    },
    methods: {
        setWaring() {
            this.info = '没有权限！请先关注该微信企业号，或联系该企业号管理员'
        }
    }
}



